import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import './Home.css';
import Headshot from './images/closeup.jpg';
import Resume from './files/cv.pdf';

const A = ({ children, ...props }) => (
  <a target="_blank" rel="noopener noreferrer" {...props}>
    {children}
  </a>
);

A.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element,
  ]).isRequired,
};

const Photo = () => (
  <div className="photo">
    <div
      className="photo-image"
      style={{ backgroundImage: `url(${Headshot})` }}
    />
  </div>
);

const About = () => {
  const school = <A href="https://vt.edu/">Virginia Tech</A>;
  const location = <A href="https://www.google.com/maps/place/San+Francisco+Bay+Area,+CA">South Bay</A>;
  const job = <A href="https://about.google/">Google</A>;
  const allergies = <Link to="/allergies">food allergies</Link>;
  const email = <A href="mailto:hi@jackcogdill.com">email</A>;
  return (
    <div className="about">
      <p>
        Hi there!
      </p>
      <p>
        I am a recent graduate of {school} where I studied Computer Science and Japanese.
        Currently I live in the {location} and am a front-end software engineer at {job}.
      </p>
      <p>
        I have a lot of {allergies}.
      </p>
      <p>
        Feel free to {email} me; happy to chat about books, anime, philosophy,
        or anything else really.
      </p>
      <p>
        Some more links:
      </p>
      <div className="contact-links">
        <A href={Resume}>rsum</A>
        <A href="https://github.com/jackcogdill">GitHub</A>
        <A href="https://codepen.io/jackcogdill">CodePen</A>
        <A href="https://www.linkedin.com/in/jackcogdill/">LinkedIn</A>
      </div>
    </div>
  );
};

const Home = () => (
  <div className="home">
    <Photo />
    <About />
  </div>
);

export default Home;



